var React = require("react");
var _ = require("../../utils");

module.exports = function(component) {
  var children = component.props.children;
  var count = React.Children.count(children);
  if(!count) { return []; }
  if(count===1) { children = [children]; }
  return _.flattenDeep(children);
};



