var utils = require("./utils");
var _ = utils;
var pathtoRegexp = require('path-to-regexp');





/**
   * Initialize `Route` with the given HTTP `path`,
   * and an array of `callbacks` and `options`.
   *
   * Options:
   *
   *   - `sensitive`    enable case-sensitive routes
   *   - `strict`       enable strict matching for trailing slashes
   *
   * @param {String} path
   * @param {Object} options.
   * @api private
   */

function Route(path, options) {
  options = options || {};
  this.path = (path === '*') ? '(.*)' : path;
  this.method = 'GET';
  this.regexp = pathtoRegexp(this.path,
    this.keys = [],
    options.sensitive,
    options.strict);
}


/**
 * Return route middleware with
 * the given callback `fn()`.
 *
 * @param {Function} fn
 * @return {Function}
 * @api public
 */

Route.prototype.middleware = function(fn){
  var self = this;
  return function(ctx, next){
    if(self.match(ctx.path, ctx.params)) { return fn(ctx, next); }
    next();
  };
};

/**
 * Check if this route matches `path`, if so
 * populate `params`.
 *
 * @param {String} path
 * @param {Array} params
 * @return {Boolean}
 * @api private
 */

Route.prototype.match = function(path, params){
  var keys = this.keys,
      qsIndex = path.indexOf('?'),
      pathname = ~qsIndex
        ? path.slice(0, qsIndex)
        : path,
      m = this.regexp.exec(decodeURIComponent(pathname));

  if (!m) { return false; }

  for (var i = 1, len = m.length; i < len; ++i) {
    var key = keys[i - 1];

    var val = (typeof m[i] === 'string')
      ? decodeURIComponent(m[i])
      : m[i];

    if (key) {
      params[key.name] = undefined !== params[key.name]
        ? params[key.name]
        : val;
    } else {
      params.push(val);
    }
  }

  return true;
};




function Context(path) {

  var i = path.indexOf('?');

  this.canonicalPath = path;
  this.path = path;//.replace(base, '') || '/';

  this.title = document.title;
  // this.state = state || {};
  // this.state.path = path;
  this.querystring = ~i
    ? path.slice(i + 1)
    : '';
  this.pathname = ~i
    ? path.slice(0, i)
    : path;
  this.params = [];

  this.query = utils.parseQueryString(this.querystring);

  // fragment
  this.hash = '';
  if (!~this.path.indexOf('#')) { return; }
  var parts = this.path.split('#');
  this.path = parts[0];
  this.hash = parts[1] || '';
  this.querystring = this.querystring.split('#')[0];
}


Context.prototype.modifyQuery = function(newParams,replace) {
  var params = _.extend({},this.query,newParams);
  var queryString = _.param(params);
  if(queryString) { queryString = "?" + queryString; }
  this.router[replace === "back" && "backTo" || replace && "replace" || "goto"](this.pathname + queryString);
};

